import React, { useEffect, useState } from 'react';
import { Button, Input } from 'reactstrap';

export const Food = ({ id, units, name, group, portion, prot, carb, fat, editable }) => {
  const [_id, setId] = useState(id);
  const [_units, setUnits] = useState(units);
  const [_name, setName] = useState(name);
  const [_group, setGroup] = useState(group);
  const [_portion, setPortion] = useState(portion);
  const [_prot, setProt] = useState(prot);
  const [_carb, setCarb] = useState(carb);
  const [_fat, setFat] = useState(fat);
  const [isEditable, setIsEditable] = useState(editable);
  const [isDone, setIsDone] = useState(false);

  useEffect(() => {
    setIsEditable(editable);
  }, [editable]);

  const calulateCalories = () => {
    return ((_carb + _prot) * 4 + _fat * 9);
  };

  const handleOnclickEdit = e => {
    setIsEditable(true);
  };

  const handleOnclickDone = e => {
    setIsEditable(false);
  };

  const handleProtOnChange = value => {
    setProt(value);
    calulateCalories();
  };

  return (
    <tr>
      <th scope="row">{_id}</th>
      <td>
        {isEditable ? (
          <Input type="text" value={_name} onChange={event => setName(event.target.value)} />
        ) : (
          _name
        )}
      </td>
      <td>
        {isEditable ? (
          <Input type="text" value={_group} onChange={event => setGroup(event.target.value)} />
        ) : (
          _group
        )}
      </td>
      <td>
        {isEditable ? (
          <Input
            type="number"
            value={_portion}
            onChange={event => setPortion(event.target.value)}
          />
        ) : (
          _portion + ' ' + _units
        )}
      </td>
      <td>{cals}</td>
      <td>
        {isEditable ? (
          <Input
            type="number"
            value={_prot}
            onChange={event => handleProtOnChange(event.target.value)}
          />
        ) : (
          _prot + ' g'
        )}
      </td>
      <td>
        {isEditable ? (
          <Input type="number" value={_carb} onChange={event => setCarb(event.target.value)} />
        ) : (
          _carb + ' g'
        )}
      </td>
      <td>
        {isEditable ? (
          <Input type="number" value={_fat} onChange={event => setFat(event.target.value)} />
        ) : (
          _fat + ' g'
        )}
      </td>
      <td>
        <Button
          color={!isEditable ? 'primary' : 'danger'}
          onClick={!isEditable ? handleOnclickEdit : handleOnclickDone}
        >
          {!isEditable ? 'edit' : 'done'}
        </Button>
      </td>
    </tr>
  );
};
